import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Header from "../components/header"
import { HeroContainer, HeroH1, HeroH2, Cursor } from "../styledComponents/hero"
import checkIfTabIsActive from "../utils/checkIfTabIsActive"

class TypeAnimation extends React.Component {
  state = { typeAnimationText: "" }

  typeAnimation = text => {
    const { typeAnimationText } = this.state
    if (typeAnimationText.length !== text.length) {
      setTimeout(() => {
        this.setState({
          typeAnimationText: text.substring(0, typeAnimationText.length + 1),
        })
        this.typeAnimation(text)
      }, 75)
    }
  }

  componentDidMount() {
    const startTypeAnimation = this.typeAnimation(this.props.text)
    checkIfTabIsActive(startTypeAnimation)
  }

  render() {
    return (
      <React.Fragment>
        {this.state.typeAnimationText}
        <Cursor big>|</Cursor>
      </React.Fragment>
    )
  }
}

